import type {
  Task,
  User,
  Notification,
  CreateTaskDto,
  UpdateTaskDto,
  LoginCredentials,
  RegisterData,
  ApiResponse,
  TaskFilters,
  TaskSort,
  DashboardStats,
} from "@/types";
import {
  mockTasks,
  mockUsers,
  mockNotifications,
  mockDashboardStats,
  mockCurrentUser,
} from "@/lib/mock-data";

/**
 * API service layer for backend communication.
 * Currently uses mock data - replace implementations with actual API calls.
 *
 * @example
 * // When connecting to backend, replace:
 * // return mockTasks;
 * // with:
 * // const response = await fetch(`${API_BASE_URL}/tasks`);
 * // return response.json();
 */

// Configure your backend URL here when connecting
const API_BASE_URL =
  process.env.NEXT_PUBLIC_API_URL || "http://localhost:3001/api";

/** Simulates network delay for realistic loading states */
const delay = (ms: number) => new Promise((resolve) => setTimeout(resolve, ms));

// ============================================
// AUTH SERVICE
// ============================================

export const authService = {
  /**
   * Authenticate user with email and password.
   * Backend: POST /api/auth/login
   */
  async login(
    credentials: LoginCredentials
  ): Promise<ApiResponse<{ user: User; token: string }>> {
    await delay(800);

    const user = mockUsers.find((u) => u.email === credentials.email);
    if (user && credentials.password === "password123") {
      return {
        data: {
          user,
          token: "mock-jwt-token-" + Date.now(),
        },
      };
    }
    return { error: "Invalid email or password" };
  },

  /**
   * Register a new user account.
   * Backend: POST /api/auth/register
   */
  async register(
    data: RegisterData
  ): Promise<ApiResponse<{ user: User; token: string }>> {
    await delay(1000);

    if (mockUsers.some((u) => u.email === data.email)) {
      return { error: "Email already registered" };
    }

    const newUser: User = {
      id: String(mockUsers.length + 1),
      email: data.email,
      name: data.name,
      avatarUrl: `https://api.dicebear.com/7.x/avataaars/svg?seed=${data.name}`,
      createdAt: new Date().toISOString(),
      updatedAt: new Date().toISOString(),
    };

    return {
      data: {
        user: newUser,
        token: "mock-jwt-token-" + Date.now(),
      },
    };
  },

  /**
   * Get current authenticated user.
   * Backend: GET /api/auth/me
   */
  async getCurrentUser(): Promise<ApiResponse<User>> {
    await delay(300);
    return { data: mockCurrentUser };
  },

  /**
   * Logout current user.
   * Backend: POST /api/auth/logout
   */
  async logout(): Promise<ApiResponse<void>> {
    await delay(200);
    return { message: "Logged out successfully" };
  },

  /**
   * Update user profile.
   * Backend: PATCH /api/auth/profile
   */
  async updateProfile(data: Partial<User>): Promise<ApiResponse<User>> {
    await delay(500);
    return {
      data: {
        ...mockCurrentUser,
        ...data,
        updatedAt: new Date().toISOString(),
      },
    };
  },
};

// ============================================
// TASK SERVICE
// ============================================

export const taskService = {
  /**
   * Get all tasks with optional filters and sorting.
   * Backend: GET /api/tasks
   */
  async getTasks(
    filters?: TaskFilters,
    sort?: TaskSort
  ): Promise<ApiResponse<Task[]>> {
    await delay(600);

    let tasks = [...mockTasks];

    // Apply filters
    if (filters) {
      if (filters.status && filters.status !== "all") {
        tasks = tasks.filter((t) => t.status === filters.status);
      }
      if (filters.priority && filters.priority !== "all") {
        tasks = tasks.filter((t) => t.priority === filters.priority);
      }
      if (filters.assignedToMe) {
        tasks = tasks.filter((t) => t.assignedToId === mockCurrentUser.id);
      }
      if (filters.createdByMe) {
        tasks = tasks.filter((t) => t.creatorId === mockCurrentUser.id);
      }
      if (filters.overdue) {
        tasks = tasks.filter(
          (t) => new Date(t.dueDate) < new Date() && t.status !== "Completed"
        );
      }
    }

    // Apply sorting
    if (sort) {
      tasks.sort((a, b) => {
        let comparison = 0;

        switch (sort.field) {
          case "dueDate":
            comparison =
              new Date(a.dueDate).getTime() - new Date(b.dueDate).getTime();
            break;
          case "priority": {
            const priorityOrder = { Urgent: 0, High: 1, Medium: 2, Low: 3 };
            comparison = priorityOrder[a.priority] - priorityOrder[b.priority];
            break;
          }
          case "status": {
            const statusOrder = {
              "To Do": 0,
              "In Progress": 1,
              Review: 2,
              Completed: 3,
            };
            comparison = statusOrder[a.status] - statusOrder[b.status];
            break;
          }
          case "createdAt":
            comparison =
              new Date(a.createdAt).getTime() - new Date(b.createdAt).getTime();
            break;
          case "title":
            comparison = a.title.localeCompare(b.title);
            break;
        }

        return sort.direction === "desc" ? -comparison : comparison;
      });
    }

    return { data: tasks };
  },

  /**
   * Get a single task by ID.
   * Backend: GET /api/tasks/:id
   */
  async getTask(id: string): Promise<ApiResponse<Task>> {
    await delay(400);

    const task = mockTasks.find((t) => t.id === id);
    if (!task) {
      return { error: "Task not found" };
    }
    return { data: task };
  },

  /**
   * Create a new task.
   * Backend: POST /api/tasks
   */
  async createTask(data: CreateTaskDto): Promise<ApiResponse<Task>> {
    await delay(500);

    const newTask: Task = {
      id: String(mockTasks.length + 1),
      ...data,
      assignedToId: data.assignedToId || null,
      creatorId: mockCurrentUser.id,
      creator: mockCurrentUser,
      assignedTo: data.assignedToId
        ? mockUsers.find((u) => u.id === data.assignedToId) || null
        : null,
      createdAt: new Date().toISOString(),
      updatedAt: new Date().toISOString(),
    };

    mockTasks.unshift(newTask);
    return { data: newTask };
  },

  /**
   * Update an existing task.
   * Backend: PATCH /api/tasks/:id
   */
  async updateTask(
    id: string,
    data: UpdateTaskDto
  ): Promise<ApiResponse<Task>> {
    await delay(400);

    const taskIndex = mockTasks.findIndex((t) => t.id === id);
    if (taskIndex === -1) {
      return { error: "Task not found" };
    }

    const updatedTask: Task = {
      ...mockTasks[taskIndex],
      ...data,
      assignedTo:
        data.assignedToId !== undefined
          ? data.assignedToId
            ? mockUsers.find((u) => u.id === data.assignedToId) || null
            : null
          : mockTasks[taskIndex].assignedTo,
      updatedAt: new Date().toISOString(),
    };

    mockTasks[taskIndex] = updatedTask;
    return { data: updatedTask };
  },

  /**
   * Delete a task.
   * Backend: DELETE /api/tasks/:id
   */
  async deleteTask(id: string): Promise<ApiResponse<void>> {
    await delay(300);

    const taskIndex = mockTasks.findIndex((t) => t.id === id);
    if (taskIndex === -1) {
      return { error: "Task not found" };
    }

    mockTasks.splice(taskIndex, 1);
    return { message: "Task deleted successfully" };
  },
};

